import { useContext, useState, useEffect, useRef } from "react";
import { endpoint } from "../Utils/enpoint";
import { AuthContext } from "../Store/AuthContext";
import axios from "axios";
import { message } from "antd";

const usePostFormData = (url, dataPost, trigger) => {
  const [onSuccess, setOnSuccess] = useState("");
  const [loading, setLoading] = useState(false);

  const { signOut } = useContext(AuthContext);
  const authContext = useContext(AuthContext);
  const source = axios.CancelToken.source();

  const handlePostData = useRef(() => {});

  handlePostData.current = async () => {
    setLoading(true);

    try {
      const response = await axios.post(`${endpoint}/api/${url}`, dataPost, {
        headers: {
          "Content-Type": "multipart/form-data",
          Authorization: `bearer ${authContext.userToken}`,
        },
        cancelToken: source.token,
      });

      if (!response) {
        throw new Error();
      }

      setLoading(false);
      setOnSuccess(`success ${new Date().getTime()}`);
      setTimeout(() => {
        setOnSuccess("");
      }, 1000);

      console.log(`post data ${url}`);
    } catch (err) {
      if (String(err) === "Cancel") {
        message.warn("Cancel post data");
        console.log("Cancel Token: ", err);

        return null;
      }

      if (err.response?.status === 500) {
        message.error(
          `${err.response.data.error.status_code}, ${err.response.data.error.message}`
        );
        signOut();

        return null;
      }

      if (err.response) {
        message.error(
          `${err.response.data.error.status_code}, ${err.response.data.error.message}`
        );
        setOnSuccess("error");
        setLoading(false);

        return null;
      }

      message.error("Something went wrong, please try again..");
      setOnSuccess("error");
      console.log(err);
      setLoading(false);
    }
  };

  useEffect(() => {
    if (url && trigger !== 0) {
      handlePostData.current();
    }

    return () => {
      source.cancel();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [url, trigger]);

  return { onSuccess, loading };
};

export default usePostFormData;
