import { Form, Input, Typography, Button, Space } from "antd";
import { FiPlus } from "react-icons/fi";

const { Text } = Typography;

const FormInputMultiple = ({
  label,
  name,
  validateStatus,
  hasFeedback,
  help,
  required,
  requiredLabel,
  message,
  prefix,
  allowClear,
  placeholder,
  value,
  onChange,
  onAdd,
  disableAdd,
  dataMultiple,
  setDataMultiple,
  ...rest
}) => {
  return (
    <>
      <Text type="secondary">{label}</Text>
      {requiredLabel && <Text type="danger">*</Text>}
      <Form.Item
        name={name}
        validateStatus={validateStatus}
        hasFeedback={hasFeedback}
        help={help}
        rules={[
          {
            required,
            message,
          },
        ]}
      >
        <Input
          prefix={prefix}
          allowClear={allowClear}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          style={{
            borderRadius: "10px",
          }}
          {...rest}
        />
      </Form.Item>
      <div
        style={{
          marginTop: -52,
          marginBottom: 20,
          marginRight: 10,
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "flex-end",
            marginBottom: 10,
          }}
        >
          <Button
            icon={<FiPlus />}
            type="primary"
            shape="round"
            size="small"
            onClick={onAdd}
            disabled={disableAdd}
          >
            add
          </Button>
        </div>
        <Space wrap>
          {dataMultiple.map((item) => (
            <Button
              key={item}
              size="small"
              shape="round"
              onClick={() =>
                setDataMultiple(dataMultiple.filter((data) => data !== item))
              }
            >
              {item}
            </Button>
          ))}
        </Space>
      </div>
    </>
  );
};

export default FormInputMultiple;
