import { Typography } from "antd";
import { labelColor } from "../../Utils/colors";
import "./component.css";

const { Text } = Typography;

const MyForm = ({label, required, children}) => {
  return (
    <div className="my-form-container">
      <div className="my-form-label">
        <Text style={{color: labelColor}}>{label}</Text>{required && (<Text type="danger">*</Text>)}
      </div>
      <div className="my-form-input">{children}</div>
    </div>
  );
};

export default MyForm;
