import { Form, Input, Typography } from "antd";

const { Text } = Typography;

const FormTextArea = ({
  label,
  name,
  required,
  requiredLabel,
  message,
  rows,
  placeholder,
  value,
  onChange,
  ...rest
}) => {
  return (
    <>
      <Text type="secondary">{label}</Text>
      {requiredLabel && <Text type="danger">*</Text>}
      <Form.Item
        name={name}
        rules={[
          {
            required,
            message,
          },
        ]}
      >
        <Input.TextArea
          rows={rows}
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          style={{
            borderRadius: "10px",
          }}
          {...rest}
        />
      </Form.Item>
    </>
  );
};

export default FormTextArea;
