import { Typography, Table, Tag, Modal, Button } from "antd";
import { FlexContainer, FlexHalf } from ".";
import {
  closeColor,
  gradBlue1,
  gradOrange1,
  gradPurple1,
  newColor,
  secondColor,
  successColor,
} from "../../Utils/colors";

const { Title, Text } = Typography;

const columnsComment = [
  {
    title: "ICT",
    dataIndex: "creator",
    key: "creator",
    render: (text, record) => {
      let isStatus;
      if (record.status === "New") {
        isStatus = { bg: newColor };
      } else if (record.status === "Assigned") {
        isStatus = { bg: secondColor };
      } else if (record.status === "Resovled") {
        isStatus = { bg: successColor };
      } else if (record.status === "Deployed") {
        isStatus = { bg: gradBlue1 };
      } else if (record.status === "Verified") {
        isStatus = { bg: gradOrange1 };
      } else if (record.status === "Reopened") {
        isStatus = { bg: gradPurple1 };
      } else {
        isStatus = { bg: closeColor };
      }
      return (
        <div style={{ display: "flex", flexDirection: "column" }}>
          <Text strong>{text}</Text>
          <div style={{ display: "flex", flexDirection: "row" }}>
            <Tag color={isStatus.bg}>{record.status}</Tag>
          </div>
          <Text type="secondary">{record.created_time}</Text>
        </div>
      );
    },
  },
  {
    title: "Comment",
    dataIndex: "comment",
    key: "comment",
  },
];

const ListContent = ({ label, children }) => (
  <>
    <Text type="secondary">{label}</Text>
    <Title level={5} style={{ marginTop: "0px", marginBottom: "14px" }}>
      {children}
    </Title>
  </>
);

const ListDetails = ({ label, children }) => (
  <div style={{ display: "flex", flexDirection: "column" }}>
    <Text type="secondary">{label}</Text>
    <Text type="secondary" style={{ marginTop: "0px", marginBottom: "14px" }}>
      {children}
    </Text>
  </div>
);

const ListStatus = ({ label, children }) => {
  let isStatus;
  if (children === "New") {
    isStatus = { bg: newColor };
  } else if (children === "Assigned") {
    isStatus = { bg: secondColor };
  } else if (children === "Resovled") {
    isStatus = { bg: successColor };
  } else if (children === "Deployed") {
    isStatus = { bg: gradBlue1 };
  } else if (children === "Verified") {
    isStatus = { bg: gradOrange1 };
  } else if (children === "Reopened") {
    isStatus = { bg: gradPurple1 };
  } else {
    isStatus = { bg: closeColor };
  }

  return (
    <div style={{ display: "flex", flexDirection: "column" }}>
      <Text type="secondary">{label}</Text>
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          marginTop: "4px",
        }}
      >
        <Tag color={isStatus.bg} style={{ marginBottom: "14px" }}>
          {children}
        </Tag>
      </div>
    </div>
  );
};

const ModalIssueDetails = ({
  title,
  visible,
  onCancel,
  buttonIcon,
  buttonOnclick,
  buttonDisabled,
  buttonTitle,
  dataTemp,
}) => {
  return (
    <Modal
      title={title}
      visible={visible}
      onCancel={onCancel}
      footer={[
        <Button
          key="modaldetails"
          type="primary"
          shape="round"
          icon={buttonIcon}
          size="large"
          onClick={buttonOnclick}
          disabled={buttonDisabled}
        >
          {buttonTitle}
        </Button>,
      ]}
      width={1000}
    >
      <FlexContainer>
        <FlexHalf>
          <ListContent label="Requestor">{dataTemp.requestor}</ListContent>
          <ListContent label="Title">{dataTemp.title}</ListContent>
          <ListDetails label="Details">{dataTemp.description}</ListDetails>
          <ListContent label="Category">{dataTemp.category_id}</ListContent>
          <ListContent label="Priority">{dataTemp.priority}</ListContent>
          {dataTemp.assigned_to && (
            <ListContent label="Assign to">{dataTemp.assigned_to}</ListContent>
          )}
        </FlexHalf>
        <FlexHalf>
          <ListStatus label="Status">{dataTemp.status_id}</ListStatus>
          <ListContent label="Created at">{dataTemp.created_time}</ListContent>
          <ListContent label="Accept time">
            {dataTemp.accepted_time}
          </ListContent>
          <ListContent label="End time">{dataTemp.end_time}</ListContent>
        </FlexHalf>
      </FlexContainer>
      <Text type="secondary">History</Text>
      <Table
        columns={columnsComment}
        dataSource={
          dataTemp?.ticket_comments[0].creator === 0
            ? []
            : dataTemp?.ticket_comments
        }
        pagination={false}
        rowKey={(record) => record.created_time}
        style={{ marginTop: "6px" }}
      />
    </Modal>
  );
};

export default ModalIssueDetails;
