import { useContext, Suspense, lazy } from "react";
import { Routes, Route } from "react-router-dom";
import Sidebar from "./Components/Sidebar";
import { AuthContext } from "./Store/AuthContext";
import { MainLoading } from "./Components";
import "./App.css";
// import { Attendance } from "./Pages";

const Splash = lazy(() => import("./Pages/Splash"));
const SignIn = lazy(() => import("./Pages/Auth"));
const Dashboard = lazy(() => import("./Pages/Dashboard"));
const Attendance = lazy(() => import("./Pages/Attendance"));
const AttendanceLog = lazy(() => import("./Pages/Attendance/AttendanceLog"));
const Reimbursement = lazy(() => import("./Pages/Reimbursement"));
const UserList = lazy(() => import("./Pages/UserManagement"));
const UserUpload = lazy(() => import("./Pages/UserManagement/UserUpload"));
const Employee = lazy(() => import("./Pages/UserManagement/Employee"));
const EmployeeUpload = lazy(() => import("./Pages/UserManagement/EmployeeUpload"));
const Division = lazy(() => import("./Pages/UserManagement/Division"));
const Religion = lazy(() => import("./Pages/UserManagement/Religion"));
const Area = lazy(() => import("./Pages/UserManagement/Area"));
const Cluster = lazy(() => import("./Pages/UserManagement/Cluster"));
const Site = lazy(() => import("./Pages/Site"));
const SiteUpload = lazy(() => import("./Pages/Site/SiteUpload"));
const RadiusConfig = lazy(() => import("./Pages/Site/RadiusConfig"));
const Profile = lazy(() => import("./Pages/Profile"));
const NotAccess = lazy(() => import("./Pages/403"));
const NotFound = lazy(() => import("./Pages/404"));

function App() {
  const authContext = useContext(AuthContext);

  return (
    <Suspense fallback={<MainLoading />}>
      {authContext.userToken === null ? (
        <Routes>
          <Route path="*" element={<Splash />} />
          <Route path="/" element={<Splash />} />
          <Route path="/auth" element={<SignIn />} />
        </Routes>
      ) : (
        <Sidebar>
          <Routes>
            <Route>
              <Route path="*" element={<NotFound />} />
              <Route path="/" element={<Dashboard />} />
              <Route path="/403" element={<NotAccess />} />
              <Route path="attendance" element={<Attendance />} />
              <Route path="attendance/log" element={<AttendanceLog />} />
              <Route path="reimbursement" element={<Reimbursement />} />
              <Route path="user-management" element={<UserList />} />
              <Route path="user-management/upload" element={<UserUpload />} />
              <Route path="user-management/employee" element={<Employee />} />
              <Route path="user-management/employee/upload" element={<EmployeeUpload />} />
              <Route path="user-management/division" element={<Division />} />
              <Route path="user-management/religion" element={<Religion />} />
              <Route path="user-management/area" element={<Area />} />
              <Route path="user-management/cluster" element={<Cluster />} />
              <Route path="site" element={<Site />} />
              <Route path="site/upload-site" element={<SiteUpload />} />
              <Route path="site/radius-config" element={<RadiusConfig />} />
              <Route path="profile" element={<Profile />} />
            </Route>
          </Routes>
        </Sidebar>
      )}
    </Suspense>
  );
}

export default App;
