import { useContext, useEffect, useRef, useState } from "react";
import { endpoint } from "../Utils/enpoint";
import { AuthContext } from "../Store/AuthContext";
import axios from "axios";
import { message } from "antd";

const useGetData = (url, updateData) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  const { signOut } = useContext(AuthContext);
  const authcontext = useContext(AuthContext);
  const source = axios.CancelToken.source();

  const getData = useRef(() => {});

  getData.current = async () => {
    setLoading(true);
    try {
      const response = await axios.get(`${endpoint}/api/${url}`, {
        headers: {
          Authorization: `bearer ${authcontext.userToken}`,
        },
        cancelToken: source.token,
      });

      if (!response) {
        throw new Error();
      }

      setData(response.data.data);
      console.log(`fetch data ${url}`);
      setLoading(false);
    } catch (err) {
      if (String(err) === "Cancel") {
        console.log("Cancel Token: ", err);

        return null;
      }

      if (err.response?.status === 500) {
        message.error(
          `${err.response.data.error.status_code}, ${err.response.data.error.message}`
        );
        signOut();

        return null;
      }

      if (err.response) {
        message.error(
          `${err.response.data.error.status_code}, ${err.response.data.error.message}`
        );
        setLoading(false);

        return null;
      }

      message.error("Something went wrong, please try again..");
      console.log(err);
      setLoading(false);
    }
  };

  useEffect(() => {
    if (url) {
      getData.current();
    }

    return () => {
      source.cancel();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [url, updateData]);

  return { data, loading };
};

export default useGetData;
