import { Select, Typography, Form } from "antd";
import "./component.css";

const { Text } = Typography;

const FormSelect = ({
  children,
  label,
  requiredLabel,
  name,
  validateStatus,
  hasFeedback,
  help,
  required,
  message,
  placeholder,
  value,
  onChange,
  loading,
  ...rest
}) => {
  return (
    <>
      {label && (
        <>
          <Text type="secondary">{label}</Text>
          {requiredLabel && <Text type="danger">*</Text>}
        </>
      )}
      <Form.Item
        name={name}
        validateStatus={validateStatus}
        hasFeedback={hasFeedback}
        help={help}
        rules={[
          {
            required,
            message,
          },
        ]}
      >
        <Select
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          style={{
            width: "100%",
          }}
          loading={loading}
          {...rest}
        >
          {children}
        </Select>
      </Form.Item>
    </>
  );
};

export default FormSelect;
