import { Select, Typography, Form } from "antd";
import { FiEdit } from "react-icons/fi";
import { successColor } from "../../Utils/colors";
import "./component.css";

const { Text } = Typography;

const FormSelectMultiple = ({
  children,
  label,
  requiredLabel,
  name,
  required,
  message,
  placeholder,
  value,
  onChange,
  loading,
  onEdit,
  onVisibleEdit,
  ...rest
}) => {
  return (
    <>
      {label && (
        <>
          <Text type="secondary">{label}</Text>
          {requiredLabel && <Text type="danger">*</Text>}
        </>
      )}
      <Form.Item
        name={name}
        rules={[
          {
            required,
            message,
          },
        ]}
      >
        <Select
          mode="multiple"
          allowClear
          placeholder={placeholder}
          value={value}
          onChange={onChange}
          style={{
            width: "100%",
          }}
          loading={loading}
          {...rest}
        >
          {children}
        </Select>
      </Form.Item>
      {onVisibleEdit && (
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "flex-end",
            cursor: "pointer",
            backgroundColor: "red",
          }}
          onClick={onEdit}
        >
          <FiEdit
            color={successColor}
            size={18}
            style={{ marginRight: 10, marginTop: -20 }}
          />
        </div>
      )}
    </>
  );
};

export default FormSelectMultiple;
