import React, { createContext, useEffect, useReducer } from "react";
import { endpoint } from "../Utils/enpoint";
import axios from "axios";

const initialLoginState = {
  isLoading: true,
  userId: null,
  userName: null,
  userEmail: null,
  userEmpId: null,
  userActive: null,
  userRole: null,
  userToken: null,
  firstDate: null,
  lastDate: null,
  signIn: () => null,
  signOut: () => null,
  signUp: () => null,
};

export const AuthContext = createContext(initialLoginState);

const loginReducer = (prevState, action) => {
  switch (action.type) {
    case "RETRIEVE_TOKEN":
      return {
        ...prevState,
        userId: action.userId,
        userName: action.username,
        userEmail: action.email,
        userEmpId: action.nik,
        userActive: action.active,
        userRole: action.role,
        userToken: action.token,
        isLoading: false,
      };
    case "LOGIN":
      return {
        ...prevState,
        userId: action.userId,
        userName: action.username,
        userEmail: action.email,
        userEmpId: action.nik,
        userActive: action.active,
        userRole: action.role,
        userToken: action.token,
        isLoading: false,
      };
    case "LOGOUT":
      return {
        ...prevState,
        userId: null,
        userName: null,
        userEmail: null,
        userEmpId: null,
        userActive: null,
        userRole: null,
        userToken: null,
        isLoading: false,
      };
    case "REGISTER":
      return {
        ...prevState,
        userId: action.userId,
        userName: action.username,
        userEmail: action.email,
        userEmpId: action.nik,
        userActive: action.active,
        userRole: action.role,
        userToken: action.token,
        isLoading: false,
      };
    case "GET_DATE":
      return {
        ...prevState,
        firstDate: action.firstDate,
        lastDate: action.lastDate,
        isLoading: false,
      };
    default:
      return {
        ...prevState,
        isLoading: false,
      };
  }
};

const AuthProvider = ({ children }) => {
  const [state, dispatch] = useReducer(loginReducer, initialLoginState);

  useEffect(() => {
    let userId = null;
    let userName = null;
    let userEmail = null;
    let userEmpId = null;
    let userActive = null;
    let userRole = null;
    let userToken = null;

    let firstDate = new Date();
    firstDate =
      firstDate.setMonth(firstDate.getMonth() - 1) &&
      firstDate.setDate(25) &&
      firstDate.setHours(0) &&
      firstDate.setMinutes(1);

    let lastDate = new Date();
    lastDate =
      lastDate.setDate(24) && lastDate.setHours(0) && lastDate.setMinutes(1);

    try {
      if (typeof window !== undefined) {
        userId = localStorage.getItem("userId");
        userName = localStorage.getItem("userName");
        userEmail = localStorage.getItem("userEmail");
        userEmpId = localStorage.getItem("userEmpId");
        userActive = localStorage.getItem("userActive");
        userRole = localStorage.getItem("userRole");
        userToken = localStorage.getItem("userToken");
      }
    } catch (err) {
      console.log(err);
    }
    console.log(
      "retrieve token: ",
      `${userId} ${userName} ${userEmail} ${userEmpId} ${userActive} ${userRole} ${userToken}`
    );

    dispatch({
      type: "RETRIEVE_TOKEN",
      userId: Number(userId),
      username: userName,
      email: userEmail,
      nik: Number(userEmpId),
      active: Number(userActive),
      role: Number(userRole),
      token: userToken,
    });

    dispatch({
      type: "GET_DATE",
      firstDate,
      lastDate,
    });
  }, []);

  const signIn = async (emailLogin, password) => {
    try {
      const dataLogin = {
        email: emailLogin,
        password,
      };

      const response = await axios.post(
        `${endpoint}/api/auth/login`,
        dataLogin
      );

      console.log("login: ", response);

      if (response.status !== 200) {
        return Promise.reject(response.data);
      }

      const { id, email, employe_id, is_active, role_id } = response.data.user;
      const userName = email ? email.substr(0, email.indexOf("@")) : "";
      const token = response.data.token;
      dispatch({
        type: "LOGIN",
        userId: id,
        username: userName,
        email: email,
        nik: employe_id,
        active: is_active,
        role: role_id,
        token: token,
      });

      if (typeof window !== undefined) {
        localStorage.setItem("userId", String(id));
        localStorage.setItem("userName", userName);
        localStorage.setItem("userEmail", email);
        localStorage.setItem("userEmpId", String(employe_id));
        localStorage.setItem("userActive", String(is_active));
        localStorage.setItem("userRole", String(role_id));
        localStorage.setItem("userToken", token);
      }

      return Promise.resolve(response.data);
    } catch (error) {
      return Promise.reject(error);
    }
  };

  const signOut = () => {
    try {
      if (typeof window !== undefined) {
        localStorage.removeItem("userId");
        localStorage.removeItem("userName");
        localStorage.removeItem("userEmail");
        localStorage.removeItem("userEmpId");
        localStorage.removeItem("userActive");
        localStorage.removeItem("userRole");
        localStorage.removeItem("userToken");
      }
    } catch (err) {
      console.log(err);
    }
    dispatch({ type: "LOGOUT" });
  };

  const signUp = () => {};

  return (
    <AuthContext.Provider
      value={{
        ...state,
        signIn,
        signOut,
        signUp,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export default AuthProvider;
